import React from "react";
import { Route, Switch } from "react-router-dom";
import ProductosState from "../../context/admin/productosState";

import CarritoHome from "./screens/carritos/CarritoHome";
import CarritoScreen from "./screens/CarritoScreen";
import Login from "./screens/login/Login";
import Ubicanos from "./screens/ubicanos/Ubicanos";


const CarritoRouter = () => {
  return (
    <>
    
       
       <ProductosState>
      <Switch>
      <Route path="/home" component={CarritoHome} />
      <Route path="/cart" component={CarritoScreen} />
      <Route path="/ubicanos" component={Ubicanos} />
      {/* <Route path="/" component={Login} /> */}

      </Switch>
      </ProductosState>
       
    </>
  );
};

export default CarritoRouter;
