import React from 'react'
import CarritoHeader from '../carritos/CarritoHeader'
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet'



const Ubicanos = () => {
    return (
        <>

        <CarritoHeader/>
        <h3 className="text-muted">Estamos ubicados en: Calle Independencia 269, miraflores </h3>
        <main className="container-fluid">
          <div className="row">
            <div className="col">
              <MapContainer center={[-12.1172569,-77.0347856]} zoom={30} scrollWheelZoom={false}>
                <TileLayer
                  attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                  url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                />
                <Marker position={[-12.1172569,-77.0347856]}>
                  <Popup>
                    A pretty CSS3 popup. <br /> Easily customizable.
                  </Popup>
                </Marker>
              </MapContainer>
            </div>
          </div>

        </main>
        </>
    )
}

export default Ubicanos
