import React from 'react'
import CarritoHeader from './CarritoHeader'
import imagen1 from "./../../../../assets/img/imagen1.jpeg";
import imagen2 from "./../../../../assets/img/imagen2.jpeg";
import imagen3 from "./../../../../assets/img/imagen3.jpeg"
import { Button, Card } from 'react-bootstrap'

const CarritoHome = () => {
  return (
    <>
      <CarritoHeader />
      <main className="container-fluid mt-1 " >
      <h1 className="text-muted text-center">Próximos lanzamientos</h1>
        <div className="row">
         
          <div className="col-12 d-flex text-center" >
         
        
            <Card style={{ width: '18rem' }}>
              <Card.Img variant="top" src={imagen1} />
              <Card.Body>
                <Card.Title>Cafe Organico</Card.Title>
                <Card.Text>
                Cafe Organico Apangoa
                </Card.Text>
                <Button variant="primary">muy pronto...</Button>
              </Card.Body>
            </Card>
           

            <Card style={{ width: '18rem' }}>
              <Card.Img variant="top" src={imagen2} />
              <Card.Body>
                <Card.Title>Edicion Clasica</Card.Title>
                <Card.Text>
                  Tostado Oscuro
                </Card.Text>
                <Button variant="primary">muy pronto...</Button>
              </Card.Body>
            </Card>
            <Card style={{ width: '18rem' }}>
              <Card.Img variant="top" src={imagen3} />
              <Card.Body>
                <Card.Title>Café Organico</Card.Title>
                <Card.Text>
                  San fernando café organico
                </Card.Text>
                <Button variant="primary">muy pronto...</Button>
              </Card.Body>
            </Card>
            <Card style={{ width: '18rem' }}>
              <Card.Img variant="top" src={imagen3} />
              <Card.Body>
                <Card.Title>Café Organico</Card.Title>
                <Card.Text>
                San fernando café organico
                </Card.Text>
                <Button variant="primary">muy pronto...</Button>
              </Card.Body>
            </Card>
           
           
            
            
          </div>
        </div>
      </main>
    </>
  )
}

export default CarritoHome
